/* eslint-disable react/jsx-props-no-spreading */
import React from "react";
import styled from "styled-components";
import { Button } from "./Utils";

type Credentials = {
  password?: string;
  username?: string;
};

type ProjectProps = {
  name: string;
  description: string;
  demoLink: string;
  sourceLink: string;
  imgSrc: string;
  imgWidth?: string;
  credentials?: Credentials;
};

const ProjectWrapper = styled.div`
  margin-bottom: 60px;
  transition: all 0.2s ease-in;
  overflow: hidden;
  border-radius: 5px;
  border: 1px solid #ebe9e93d;

  h3 {
    color: #64c064;
    font-weight: bold;
  }

  &:hover {
    background-color: #4b4b4b1f;
    box-shadow: 0px 0px 6px 0px #e8e8e840;
  }
`;

const ProjectDetails = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 30px 0;
`;

const ProjectTitle = styled.h3`
  font-size: 25px;
  text-align: center;
  padding: 15px 0;
  background-color: #ebe9e93d;
  border-bottom: 1px solid #64c064;
`;

const ProjectDescription = styled.p`
  font-size: 16px;
  width: 40%;
  line-height: 1.8rem;
`;

type ImgProps = {
  width?: string;
};

const ProjectImg = styled.div<ImgProps>`
  width: 40%;
  text-align: center;

  img {
    display: inline-block;
    width: ${({ width }) => width || "100%"};
    border-radius: 4px;
    border: 1px solid #ebe9e93d;
  }
`;

const ProjectActions = styled.div`
  display: flex;
  justify-content: space-around;
  padding: 15px 20%;
  border-top: 1px solid #ebe9e93d;
`;

const CredentialsContainer = styled.div`
  font-size: 10px;
  padding: 6px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
`;

const ProjectCredentials = ({ username, password }: Credentials) => (
  <CredentialsContainer>
    {username && <p>username: {username}</p>}
    {password && <p>password: {password}</p>}
  </CredentialsContainer>
);

const Project = ({
  name,
  description,
  demoLink,
  sourceLink,
  credentials,
  imgSrc,
  imgWidth,
}: ProjectProps): JSX.Element => (
  <ProjectWrapper>
    <ProjectTitle>{name}</ProjectTitle>
    <ProjectDetails>
      <ProjectDescription>{description}</ProjectDescription>
      <ProjectImg width={imgWidth}>
        <img alt={`Sebastian Gil Project ${name}`} src={imgSrc} />
      </ProjectImg>
    </ProjectDetails>
    <ProjectActions>
      <Button href={demoLink} target="_blank" rel="noreferrer">
        Demo
      </Button>
      <Button href={sourceLink} target="_blank" rel="noreferrer">
        Source Code
      </Button>
      {credentials && <ProjectCredentials {...credentials} />}
    </ProjectActions>
  </ProjectWrapper>
);

Project.defaultProps = {
  credentials: null,
  imgWidth: "",
};

ProjectCredentials.defaultProps = {
  password: null,
  username: null,
};

export default Project;
