/* eslint-disable react/jsx-props-no-spreading */
import React from "react";
import styled, { css } from "styled-components";
import { Button } from "./Utils";

type ArticleProps = {
  title: string;
  text: string;
  created_at: string;
  updated_at: string;
};

type TextProps = {
  isOpen: boolean;
};

const Wrapper = styled.div`
  margin-bottom: 60px;
  transition: all 0.2s ease-in;
  overflow: hidden;
  border-radius: 5px;
  border: 1px solid #ebe9e93d;

  h3 {
    color: #64c064;
    font-weight: bold;
  }

  &:hover {
    background-color: #4b4b4b1f;
    box-shadow: 0px 0px 6px 0px #e8e8e840;
  }
`;

const Details = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px;
`;

const Title = styled.h3`
  font-size: 25px;
  text-align: center;
  padding: 15px 0;
  background-color: #ebe9e93d;
  border-bottom: 1px solid #64c064;
`;

const Text = styled.p<TextProps>`
  font-size: 16px;
  width: 100%;
  line-height: 1.8rem;
  margin-bottom: 30px;
  ${({ isOpen }) =>
    !isOpen &&
    css`
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
    `}
`;

const ReadMore = styled(Button)`
  background-color: #64c064;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
`;

const Article = ({
  title,
  text,
  created_at,
  updated_at,
}: ArticleProps): JSX.Element => {
  const [isOpen, setIsOpen] = React.useState(false);

  const handleClick = (): void => {
    setIsOpen(!isOpen);
  };

  return (
    <Wrapper>
      <Title>
        {title} - {created_at}
      </Title>
      <Details>
        <Text isOpen={isOpen}>{text}</Text>
        <ReadMore onClick={handleClick}>
          {isOpen ? "Close" : "Open article"}
        </ReadMore>
      </Details>
    </Wrapper>
  );
};

export default Article;
