/* eslint-disable react/jsx-props-no-spreading */
import React from "react";
import styled from "styled-components";
import { useQuery } from "react-query";
import axios from "axios";
import SectionWrapper, { Accent, Title } from "../Components/Utils";
import Article from "../Components/Article";

axios.defaults.baseURL = "https://sebgil-api.herokuapp.com/api";

const Wrapper = styled(SectionWrapper)`
  margin: 150px 0 60px;
`;

type ErrorType = {
  message: string;
};

type ArticleType = {
  id: number;
  type: string;
  attributes: {
    title: string;
    text: string;
    created_at: string;
    updated_at: string;
  };
  relationships: any | null;
};

interface ResultsInterface {
  data: ArticleType[] | undefined;
  isLoading: boolean;
  error: ErrorType | null;
}

const Articles: React.FC = () => {
  const { isLoading, error, data }: ResultsInterface = useQuery(
    "articles",
    () => axios.get("articles").then((res) => res.data.data)
  );

  return (
    <Wrapper>
      <Title>
        My
        <Accent> Articles</Accent>
      </Title>
      {isLoading && <p>Loading...</p>}
      {error && <p>Error: {error.message}</p>}
      {data?.map((p: ArticleType) => {
        return <Article {...p.attributes} />;
      })}
      {!isLoading && !error && !data && <p>No articles found</p>}
    </Wrapper>
  );
};

export default Articles;
