import React from "react";
import styled from "styled-components";
import GitHubSVG from "../assets/github_icon.svg";
import LinkedInSVG from "../assets/linkedin_icon.svg";

const SocialIcon = styled.a`
  background-color: white;
  border-radius: 50%;
  color: black;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  max-height: 20px;
  padding: 10px;
  margin-right: 30px;
  transition: all 0.2s ease-in-out;

  &:hover {
    box-shadow: 0px 0px 7px 0px #64c064;
  }

  img {
    display: inline-block;
    height: 20px;
    width: 20px;
  }
`;

const Social = (): JSX.Element => (
  <div>
    <SocialIcon href="https://github.com/sebGilR" target="_blank">
      <img alt="sebGilR" src={GitHubSVG} />
    </SocialIcon>
    <SocialIcon
      href="https://www.linkedin.com/in/sebastiangilrodriguez/"
      target="_blank"
    >
      <img alt="sebastiangilrodriguez" src={LinkedInSVG} />
    </SocialIcon>
  </div>
);

export default Social;
