import React from "react";
import styled from "styled-components";

const ItemsContainer = styled.ul`
  padding-left: 15px;
`;

const ListWrapper = styled.div`
  padding: 10px 0;
`;

const Description = styled.p`
  margin: 10px 0;
  color: #64c064;
`;

const Item = styled.li`
  padding-left: 5px;
  margin: 10px 5px;
  line-height: 25px;

  &::before {
    content: "👉";
    margin-right: 15px;
    font-size: 15px;
    vertical-align: text-top;
  }
`;

type ListProps = {
  description?: string;
  items: string[];
};

const List = ({ description, items }: ListProps): JSX.Element => (
  <ListWrapper>
    {description && <Description>{description}:</Description>}
    <ItemsContainer>
      {items.map((i) => (
        <Item>{i}</Item>
      ))}
    </ItemsContainer>
  </ListWrapper>
);

List.defaultProps = {
  description: null,
};

export default List;
